import { Component, OnInit } from '@angular/core';
import { ClrForm, ClrLoadingState } from '@clr/angular';
import '@clr/icons';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { VehicleSelectService } from './vehicle-select.service';
import { LoadSelect, Load, Result } from '../../interface';
import { BrowserModule } from '@angular/platform-browser';
import { catchError } from 'rxjs/operators';
import { throwError } from 'rxjs';

@Component({
  selector: 'app-vehicle-select',
  templateUrl: './vehicle-select.component.html',
  styleUrls: ['./vehicle-select.component.css']
})
export class VehicleSelectComponent implements OnInit {
  cloneButtonState: ClrLoadingState = ClrLoadingState.DEFAULT;
  loadFormGroup: any
  public totalLoad: Array<LoadSelect> = [];
  vehicle = "";
  constructor(
    private _formBuilder: FormBuilder,
    private _vehicleSelectService: VehicleSelectService
  ) {
    }

  ngOnInit(): void {
    this.loadFormGroup = this._formBuilder.group({
      length: ['', Validators.required],
      width: ['', Validators.required],
      height: ['', Validators.required],
      number: ['', Validators.required]
    });
  }

  submit() {
    this.vehicle =""
    if (this.loadFormGroup.valid) {
      
      this._vehicleSelectService.getVehicle(this.totalLoad)
        .pipe(
          catchError(error => {
            console.log(error)
            this.vehicle = "Cannot find a suitable vehicle";
            return throwError(error);
          }))
        .subscribe(result => {
          let response = result as Result;
          this.vehicle = response.result;
        });

    }
  }
  isValid() {
    if (this.totalLoad.length > 0) {
      return false;
    }
    return true;
  }

  addLoad() {
    let load: Load = {
      length : this.loadFormGroup.get('length').value,
      width: this.loadFormGroup.get('width').value,
      height: this.loadFormGroup.get('height').value
    };
    this.totalLoad.push({
      length: this.loadFormGroup.get('length').value,
      width: this.loadFormGroup.get('width').value,
      height: this.loadFormGroup.get('height').value,
      number: this.loadFormGroup.get('number').value
    });
  }

  }


