import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { VehicleSelectComponent } from './vehicle-select/vehicle-select/vehicle-select.component';
import { VehicleSelectModule } from 'src/app/vehicle-select/vehicle-select/vehicle-select.module';

const routes: Routes = [
  { path: '', redirectTo:'/vehicle-select',pathMatch:'full' },
  {path:'vehicle-select',component:VehicleSelectComponent}
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
